.onirix {
    background-color: black;
    color: white;
}

.phase {
    margin-left: 80px;
    margin-bottom: 20px;
    width: 95px;
    height: 26px;
    font-family: 'ABeeZee';
    font-style: italic;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    display: flex;
    align-items: center;
    letter-spacing: 0.3em;
    text-transform: uppercase;

    color: rgba(184, 184, 184, 0.7);
}

h2 {
    margin-left: 80px;
    margin-top: -5px;
