@import url(//db.onlinewebfonts.com/c/2f56ec659663c922a1a776c3a3760529?family=Ginra+Sans);
@import url(//db.onlinewebfonts.com/c/2f56ec659663c922a1a776c3a3760529?family=Ginra+Sans);
@import url(//db.onlinewebfonts.com/c/2f56ec659663c922a1a776c3a3760529?family=Ginra+Sans);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.intro{
    background-image: url("/pain.jpeg");
    background-position: center;
    background-color: black;
    background-repeat: no-repeat;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    vertical-align: auto;
    height: 100vh;
}

.intro{
    display: flex;
    flex-direction: row;
    color: white;
    text-align: center;
}

.slo-left, .slo-right {
    flex: 50% 1;
    font-size: 300%;
    font-style: italic;
    font-family: "Ginra Sans";
}

.slo-left h3 {
    margin: 45% 40% 10% 15%;
}

.slo-right h3 {
    margin: 35% 15% 10% 40%;
}


.Title {
    background-image: url("/background.png");
    justify-content: center;
    vertical-align: auto;
    height: 100vh;
}


@layer one {
    .Title h1 {
        position:relative;
        z-index: 1;
        padding-top: 400px;
        text-align: center;
        font-size: 300%;
        font-family: "Ginra Sans";
        color: white;
    }

    .Title h1 span {
        color: purple;
    }

    .Title .arrow {
        color: white;
        font-size: 200%;
        text-align: center;
        padding-bottom: 500px;
        font-style:normal !important;
    }
}

@layer two {

    .Title #quote p {
        text-align: center;
        color: white;
        font-size: 200%;
        font-style: italic;
        -webkit-transform: translate(0, 10vh);
                transform: translate(0, 10vh);
        transition: all 1s;
    }
    .Title .quote-idle p {
        position: relative;
        opacity: 0;
    }
    .Title .quote-disappear p{
        position:relative;
        opacity: 0;
        -webkit-transform: translate(0, 10vh);
                transform: translate(0, 10vh);
    }

    .quote-appear {
        opacity: 1;
        -webkit-transform: translate(0, 0);
                transform: translate(0, 0);
    }
}

.header {
    margin-left: 300px;
    margin-top: 60px;

    font-family: 'ABeeZee';
    font-style: italic;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    display: flex;
    align-items: center;
    letter-spacing: 0.3em;
    text-transform: uppercase;
    color: rgba(184, 184, 184, 0.7);
}

.description h3 {
    color: white;
    margin-top: 10px;
    margin-left: 300px;
    margin-bottom: 20px;
    font-family: 'Readex Pro', sans-serif;
    font-style: italic;
    font-weight: 400;
    font-size: 62px;
    line-height: 74px;
    display: flex;
    align-items: center;
    text-transform: capitalize;
}

svg {
    margin-top: -30px;
