.holster {
    position: relative;
    scroll-snap-type: both mandatory;
}

.container {
    scroll-snap-align: start;
}


@layer four {
    .navbar {
        background-color: #1a202c;
        height: 80px;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 1.2rem;
        position: relative;
        top: 0;
        z-index: 999;
    }
    
    .navbar ul {
        display: flex;
        justify-content: center;
        align-items: center;
        list-style: none;
        text-align: center;
    }
    
    .navbar li {
        margin: 10%;
    }
    
    .navbar .elem a {
        border-radius: 10px;
        padding: 20%;
        transition-property: all;
        transition-duration: 0.7s;
    }
    
    a {
        text-decoration: none;
        color:white;
    }
    
    .navbarElem .logo-elem {
        padding-right: 100%;
    }
    
    .navbar .user-elem{
        padding-left: 100%;
    }
    
    .navbar .elem:hover a{
        background-color: white;
        color: #1a202c;
    }
}

body {
    background-color: #16161A;
}


